import {Component, Input, OnInit} from '@angular/core';
import {ProblemToSolve} from '../../../../interfaces/problem-to-solve';
import {mentorUseCaseData} from '../../../../data/mentorUseCaseData';

@Component({
  selector: 'app-mentor-skills-list',
  templateUrl: './mentor-skills-list.component.html',
  styleUrls: ['./mentor-skills-list.component.scss']
})
export class MentorSkillsListComponent implements OnInit {
  @Input() mentorProblemsToSolve: string[];
  public filteredProblems: ProblemToSolve[];

  constructor() { }

  ngOnInit() {
    if (this.mentorProblemsToSolve.length) {
      this.filteredProblems = this.mentorProblemsToSolve.reduce((result: ProblemToSolve[], problem: string) => {
        result.push({
          key: problem,
          name: mentorUseCaseData[problem].name,
          desc: mentorUseCaseData[problem].desc
        });
        return result;
      }, []);
    }
  }

}
