import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {MainPageComponent} from './pages/main-page/main-page.component';
import {MentorPageComponent} from './pages/mentor-page/mentor-page.component';
import {MenteePageComponent} from './pages/mentee-page/mentee-page.component';

const routes: Routes = [
  { path: '', component: MentorPageComponent },
  { path: 'mentor', component: MentorPageComponent },
  { path: 'mentee:id', component: MenteePageComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
